@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Rouble";
  font-display: swap;
  src: url("../fonts/rouble.woff2") format("woff2"), url("../fonts/rouble.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat Regular";
  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat Medium";
  font-display: swap;
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat Semi Bold";
  font-display: swap;
  src: url("../fonts/montserrat-semi-bold.woff2") format("woff2"), url("../fonts/montserrat-semi-bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat Bold";
  font-display: swap;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  overflow: overlay;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.bg-img {
  position: relative;
}

.bg-img img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  box-sizing: border-box;
  padding: 0 20px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.header {
  position: absolute;
  width: 100%;
  padding-top: 32px;
  z-index: 1;
}
@media (max-width: 1023px) {
  .header {
    padding-top: 21px;
  }
}
@media (max-width: 767px) {
  .header {
    padding-top: 19px;
  }
}
@media (max-width: 767px) {
  .header.no-js {
    position: relative;
    padding-top: 0;
  }
}

.header__logo {
  z-index: 2;
}
@media (max-width: 1023px) {
  .header__logo {
    width: 38px;
    height: 20px;
    margin-top: 9px;
  }
}
@media (max-width: 767px) {
  .header__logo {
    width: 42px;
    height: 22px;
    margin-top: 0;
  }
}

.header__logo svg {
  fill: #ffffff;
}
@media (hover: hover) {
  .header__logo svg:hover:not(.focus-visible) {
    fill: #ffd801;
  }
}
.header__logo svg.focus-visible:focus {
  fill: #ffd801;
}
@media (max-width: 1023px) {
  .header__logo svg {
    width: 38px;
    height: 20px;
  }
}

.header__container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  min-height: 38px;
}
@media (max-width: 767px) {
  .header__container.no-js {
    display: none;
  }
}

.header__menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .header__menu {
    flex-direction: column;
    justify-content: center;
  }
}

@media (max-width: 767px) {
  .menu__nav {
    display: none;
  }
}

.menu__list {
  display: flex;
  flex-wrap: wrap;
  gap: 33px;
  list-style: none;
  padding: 0;
  margin: 0;
}
@media (max-width: 1023px) {
  .menu__list {
    gap: 24px;
  }
}
@media (max-width: 767px) {
  .menu__list {
    flex-direction: column;
  }
}

.menu__list-item a {
  font-family: "Montserrat Medium", "Arial", sans-serif;
  font-size: 16px;
  line-height: 19px;
  color: #ffffff;
  cursor: pointer;
}
@media (hover: hover) {
  .menu__list-item a:hover:not(.focus-visible) {
    color: #ffd801;
  }
}
.menu__list-item a.focus-visible:focus {
  color: #ffd801;
}
@media (max-width: 1023px) {
  .menu__list-item a {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .menu__list-item a {
    font-size: 18px;
    line-height: 22px;
    color: #011C40;
  }
}

.menu__toggle {
  display: none;
}
@media (hover: hover) {
  .menu__toggle:hover:not(.focus-visible) {
    stroke: #ffd801;
  }
}
.menu__toggle.focus-visible:focus {
  stroke: #ffd801;
}
@media (max-width: 767px) {
  .menu__toggle {
    display: block;
    background-color: transparent;
    border: none;
    stroke: #ffffff;
    padding: 0;
    cursor: pointer;
    z-index: 2;
  }
}

.sidebar {
  display: none;
}
.sidebar.active {
  display: block;
  position: absolute;
  top: 0;
  background-color: rgba(0, 43, 102, 0.5);
  width: 100%;
  height: 560px;
  transition: all 0.5s linear 0s;
}
@media (max-width: 767px) {
  .sidebar.no-js {
    display: block;
  }
}

.sidebar__aside {
  right: -100%;
  visibility: hidden;
  opacity: 0;
  transition: all 0.5s ease 0s;
}
.sidebar__aside.active {
  width: 100%;
  height: 560px;
  z-index: 3;
  position: absolute;
  top: 0;
  right: 0;
  visibility: visible;
  opacity: 1;
}
@media (max-width: 767px) {
  .sidebar__aside.no-js {
    visibility: visible;
    opacity: 1;
    right: 0;
  }
}

.sidebar__container {
  position: absolute;
  width: 285px;
  height: 560px;
  top: 0;
  right: 0;
  background-color: #ffffff;
  z-index: 4;
}
@media (max-width: 767px) {
  .sidebar__container.no-js {
    width: 100%;
    height: 300px;
  }
}

.sidebar__wrapper {
  padding: 20px 15px 0 15px;
}

.sidebar__icons {
  display: flex;
  justify-content: space-between;
  padding-bottom: 35px;
}

.sidebar__logo {
  width: 42px;
  height: 22px;
  fill: #011C40;
  cursor: pointer;
}
@media (hover: hover) {
  .sidebar__logo:hover:not(.focus-visible) {
    fill: #ffd801;
  }
}
.sidebar__logo.focus-visible:focus {
  fill: #ffd801;
}

.sidebar__toggle {
  border: none;
  background-color: transparent;
  cursor: pointer;
  padding: 0;
}
@media (hover: hover) {
  .sidebar__toggle:hover:not(.focus-visible) {
    fill: #ffd801;
  }
}
.sidebar__toggle.focus-visible:focus {
  fill: #ffd801;
}
@media (max-width: 767px) {
  .sidebar__toggle.no-js {
    display: none;
  }
}

.sidebar__list {
  display: flex;
  flex-direction: column;
  gap: 26px;
  list-style: none;
  padding: 0;
  margin: 0;
  padding-left: 10px;
}

.sidebar__list-item a {
  font-family: "Montserrat Medium", "Arial", sans-serif;
  font-size: 18px;
  line-height: 22px;
  color: #011C40;
}
@media (hover: hover) {
  .sidebar__list-item a:hover:not(.focus-visible) {
    color: #ffd801;
  }
}
.sidebar__list-item a.focus-visible:focus {
  color: #ffd801;
}

@media (max-width: 767px) {
  .main.no-js {
    position: absolute;
    top: 300px;
    width: 100%;
  }
}

.main__container {
  z-index: 0;
  padding-left: 115px;
}
@media (max-width: 1023px) {
  .main__container {
    padding-left: 20px;
  }
}
@media (max-width: 767px) {
  .main__container {
    padding-left: 15px;
  }
