  display: grid;
  grid-template-columns: 1fr 400px;
  grid-template-areas: "content form";
  grid-column-gap: 140px;
}
@media (max-width: 1023px) {
  .booking__container {
    grid-template-columns: 1fr;
    grid-template-areas: "content" "form";
  }
}

.booking__content {
  grid-area: content;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (max-width: 1023px) {
  .booking__content {
    align-items: center;
    margin-bottom: 40px;
  }
}

.booking__content h2 {
  font-family: "Montserrat Bold", "Arial", sans-serif;
  font-size: 40px;
  line-height: 60px;
  color: #011C40;
  padding: 0;
  margin: 0;
}
@media (max-width: 767px) {
  .booking__content h2 {
    font-size: 24px;
    line-height: 31px;
    padding-bottom: 25px;
    text-align: center;
  }
}

.booking__content p {
  font-family: "Montserrat Regular", "Arial", sans-serif;
  font-size: 18px;
  line-height: 27px;
  color: #011C40;
  padding: 0;
  margin: 0;
}
@media (max-width: 1023px) {
  .booking__content p {
    text-align: center;
  }
}
@media (max-width: 767px) {
  .booking__content p {
    font-size: 16px;
    line-height: 22px;
  }
