.booking__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: #E3ECF6;
}

form {
  grid-area: form;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #011C40;
  padding: 50px;
}
@media (max-width: 1023px) {
  form {
    width: 400px;
    margin: 0 auto;
  }
}
@media (max-width: 767px) {
  form {
    width: 290px;
    padding: 40px 30px;
  }
}

form h2 {
  font-family: "Montserrat Medium", "Arial", sans-serif;
  font-size: 18px;
  line-height: 27px;
  color: #ffffff;
  padding: 0;
  margin: 0 0 35px 0;
  text-align: center;
}

.form__field {
  width: 100%;
  margin-bottom: 20px;
  border-color: #ffffff;
}
@media (hover: hover) {
  .form__field:hover:not(.focus-visible) {
    border-color: #ffd801;
  }
}
.form__field.focus-visible:focus {
  border-color: #ffd801;
}

.form__field input {
  width: 100%;
  border: 1px solid transparent;
  padding: 10px 0 10px 16px;
}
@media (hover: hover) {
  .form__field input:hover:not(.focus-visible) {
    border: 1px solid #ffd801;
  }
}
.form__field input.focus-visible:focus {
  border: 1px solid #ffd801;
}

.form__field input::placeholder {
  font-family: "Montserrat Regular", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  color: rgba(1, 28, 64, 0.7);
}

.form__field.agree {
  display: flex;
  position: relative;
}

.form__field.agree label {
  font-family: "Montserrat Regular", "Arial", sans-serif;
  font-size: 10px;
  line-height: 12px;
  color: #ffffff;
  padding-left: 2.2em;
}

.form__field.agree input {
  width: 18px;
  height: 18px;
  position: absolute;
  appearance: none;
  z-index: 1;
}

.form__field.agree span {
  position: absolute;
  width: 18px;
  height: 18px;
  background: url("../img/svg/checkbox-unchecked.svg") 0 0 no-repeat;
  margin-left: -0.2em;
}

.form__field.agree input:checked + span {
  background: url("../img/svg/checkbox-checked.svg") 0 0 no-repeat;
}

form button {
  font-family: "Montserrat Semi Bold", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  color: #011C40;
  width: 100%;
  background-color: #ffffff;
  border: none;
  padding: 15px 0;
  cursor: pointer;
}
@media (hover: hover) {
  form button:hover:not(.focus-visible) {
    color: #ffd801;
  }
}
form button.focus-visible:focus {
  color: #ffd801;
}

.contacts__container {
  display: grid;
  grid-template-areas: "map address";
  grid-template-columns: 1fr 1fr;
  grid-gap: 100px;
  margin-top: 120px;
  margin-bottom: 120px;
