    height: 335px;
  }
}
@media (max-width: 767px) {
  .contacts__map iframe {
    height: 316px;
  }
}

.contacts__wrapper {
  display: flex;
  flex-direction: column;
  grid-area: address;
  padding-top: 75px;
}
@media (max-width: 1023px) {
  .contacts__wrapper {
    padding: 0;
  }
}

.contacts__title {
  font-font: "Montserrat Medium", "Arial", sans-serif;
  font-size: 18px;
  line-height: 27px;
  color: #011C40;
  margin: 0 0 28px;
