}

.contacts a {
  font-family: "Montserrat Regular", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  color: #011C40;
  cursor: pointer;
  padding-bottom: 20px;
}
@media (hover: hover) {
  .contacts a:hover:not(.focus-visible) {
    fill: #ffd801;
  }
}
.contacts a.focus-visible:focus {
  fill: #ffd801;
}

.contacts a span {
  padding-left: 6px;
}

