.footer {
  min-height: 138px;
  background-color: $color-tangaroa;
}

.footer__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 20px;

  @media (max-width: $vp-767) {
    flex-direction: column;
    align-items: flex-start;
  }
}

.footer__logo {
  width: 70px;
  height: 38px;
  cursor: pointer;

  @media (max-width: $vp-1023) {
    width: 38px;
    height: 20px;
  }

  @media (max-width: $vp-767) {
    padding-bottom: 30px;
  }
}

.footer__logo svg {
  fill: $color-default-white;

  @include hover-focus {
    fill: $color-school-bus-yellow;
  }

  @media (max-width: $vp-1023) {
    width: 38px;
    height: 20px;
  }
}

.footer-menu {
  display: flex;
  flex-wrap: wrap;
  gap: 42px;
  margin-left: 40px;
  list-style: none;
  flex-grow: 1;
  padding: 0;

  @media (max-width: $vp-767) {
    margin: 0 0 30px;
    gap: 23px;
    flex-direction: column;
  }
}

.footer-menu__item a {
  font: {
    family: $ff-montserrat-medium;
    size: 15px;
  }
  line-height: 19px;
  color: $color-default-white;
  cursor: pointer;

  @include hover-focus {
    color: $color-school-bus-yellow;
  }

  @media (max-width: $vp-1023) {
    font: {
      size: 12px;
    }
    line-height: 15px;
  }
}

.footer__accounts {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 27px;
  list-style: none;
  margin: 0;
  padding: 10px 0 0;

  @media (max-width: $vp-767) {
    padding: 0;
  }
}

.footer__accounts-item {
  width: 20px;
  height: 20px;

  @include hover-focus {
    color: $color-school-bus-yellow;
  }

  @media (max-width: $vp-1023) {
    width: 16px;
    height: 16px;
  }
}

.footer__accounts-item.ok {
  width: 12px;
  height: 20px;

  @media (max-width: $vp-1023) {
    width: 10px;
    height: 16px;
  }
}

.footer__accounts-item.dzen,
.footer__accounts-item.reddit {
  width: 12px;
  height: 20px;

  @media (max-width: $vp-1023) {
    width: 16px;
    height: 16px;
  }
}

.footer__accounts-item svg {
  fill: $color-default-white;

  width: 20px;
  height: 20px;

  @include hover-focus {
    fill: $color-school-bus-yellow;
  }

  @media (max-width: $vp-1023) {
    width: 16px;
    height: 16px;
  }
}

